ion-card {
    margin: 30px 0 !important;
    width: 100% !important;
}
.breach-head {
    padding-top: 10px;
    width: 100%;
    display: inline-block;
    clear: both;
}
.breach-head .breach-name {
    max-width: calc(100% - 80px);
    float: left;
    height: 40px;
    width: 100%;
    min-height: 90%;
    text-align: center;
    display: table;
    padding-left: 5px;
    padding-right: 5px;
}
.breach-head .breach-name h1 {
    display: table-cell;
    vertical-align: middle;
}
.breach-image-holder {
    float: right;
    width: 80px;
    min-height: 40px;
    height: 100%;
    position: relative;
}
.breach-image-holder img {
    max-height: 40px;
    max-width: 80px;
    position: absolute;
    margin: auto;
    width: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
